import axios from "./axios";
import qs from "qs"

// 登录
export const login = (name,pswd) => axios.post("/api/login",{name,pswd})
export const checkToken = () => axios.get("/checkToken")

// 发表文章
export const addArticle = (id,gid,title,md,cid) => axios.post("/addArticle",qs.stringify({id,gid,title,md,cid}))

// 查询文章
export const getArticle = (params) => axios.get("/api/getArticle",{params:params})

// 按id查询
export const getArticleById = (id) => axios.get("/api/getArticleById",{params:{id:id}})

export const removeArtilceById = (id) => axios.post("/removeArtilceById",{aid:id})

export const updateArtilceById = dada => axios.post("/updateArtilceById",dada)


// 获取分组
export const getGroup = () => axios.get("/api/getGroup")

// 添加子栏目
export const addGroup = (name,cid) => axios.post("/addGroup",{name:name,cid:cid})

// 删除子栏目
export const removeGroup = (gid) => axios.post("/removeGroup",{gid:gid})

export const modifyGroupShow = (gid,show) => axios.post("/modifyGroupShow",{gid:gid,show:show})

export const modifyColumnShow = (cid,show) => axios.post("/modifyColumnShow",{cid:cid,show:show})

export const uploadArticleImg = (formData) => axios.post("/uploadArticleImg",formData,{
    headers: { 'Content-Type': 'multipart/form-data' }
})

