<template>
    <div class="welcomeView">
        <div class="loading">
            <div class="loadingA"></div>
            <div class="loadingB"></div>
        </div>
        <div class="textView">
            <div v-for="(item,index) in str " :key="index" :style="{'animation-delay': (index*.2) +'s'}"> {{ item }} </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'welcome',
        data(){
            return {
                str:"LOADING..."
            }
        }
        
    }
</script>


<style lang="scss" scoped>
    
    .welcomeView {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: #000;
        background-size: cover;
        z-index: 999;
        .textView {
            width: 100%;
            text-align: center;
            display: flex;
            justify-content: center;
            >div {
                font-size: 30px;
                color: rgba(0,0,0,.9);
                transition: all .5s;
                animation: jump 2s infinite;
            }
        }

        .loading {
            width: 60px;
            height: 60px;
            position: relative;
            margin-bottom: 20px;
            // background: #000;
            >div {
                border-radius: 50%;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                margin: auto;
                transform-origin:center ;
                border:2px solid rgba(0,0,0,0) ;
            }
            .loadingA {
                width: 60px;
                height: 60px;
                border-left: 2px solid #156bc2;
                border-top: 2px solid #156bc2;
                animation: animation-radius 2s linear infinite;
                &::after {
                    content: "";
                    position: absolute;
                    top: 5px;
                    left: 46px;
                    width: 4px;
                    height: 4px;
                    background: #156bc2;
                    border-radius: 50%;
                    box-shadow: 0 0 2px 1px #156bc2;
                }
            }  

            .loadingB {
                width: 30px;
                height: 30px;
                border-right: 2px solid #067554;
                border-bottom: 2px solid #067554;
                animation: animation-radius-desc 2s linear infinite;
                 &::after {
                    content: "";
                    position: absolute;
                    top: 1px;
                    left: 21px;
                    width: 4px;
                    height: 4px;
                    background: #067554;
                    border-radius: 50%;
                    box-shadow: 0 0 2px 1px #067554;
                }
            }
        }
    }

    @keyframes jump{
        0%{
            transform: translateY(0px);
            margin-left: 0px;
        }
        25%{
            color: #fff;
            margin-left: 2px;
            transform: translateY(-2px);
        }
        100%{
            transform: translateY(0px);
            margin-left: 0px;
        }
    }

    @keyframes animation-radius {
        0%{
            transform: rotate(0);

        }
        100%{
            transform: rotate(360deg);

        }
    }

    @keyframes animation-radius-desc {
        0%{
            transform: rotate(0);

        }
        100%{
            transform: rotate(-360deg);

        }
    }


</style>