<template>
    <div class="detailView">
        <vue-particles v-if="!show.loading"
                color="#409EFF"
                :particleOpacity="0.7"
                :particlesNumber="60"
                shapeType="circle"
                :particleSize="6"
                linesColor="#409EFF"
                :linesWidth="1"
                :lineLinked="true"
                :lineOpacity="0.4"
                :linesDistance="150"
                :moveSpeed="3"
                :hoverEffect="true"
                hoverMode="grab"
                :clickEffect="true"
                clickMode="push">
        </vue-particles>

        <div class="detailContent"  >
            <div class="loadingView" v-show="show.loading">
                <div class="loading"></div>
            </div>

            <div class="detailMain" v-show="!show.loading">
                <div class="topMenu">
                   <span @click="$router.back()">返回</span>  > 详情
                </div>

                <div class="title">{{ articleData.a_title }}</div>
                <div id="editorView"></div>
            </div>
        </div> 
        <myFooter />
    </div>
    
</template>

<script>
import { getArticleById } from "../axios/api"
import myFooter from "../components/MyFooter.vue"
import Viewer from '@toast-ui/editor/dist/toastui-editor-viewer';
import '@toast-ui/editor/dist/toastui-editor-viewer.css';
import 'prismjs/themes/prism.css';
import '@toast-ui/editor-plugin-code-syntax-highlight/dist/toastui-editor-plugin-code-syntax-highlight.css';
// Import prismjs
import Prism from 'prismjs';
import codeSyntaxHighlight from '@toast-ui/editor-plugin-code-syntax-highlight';

export default {
    components:{
        myFooter
    },
    data(){
        return {
            show:{
                loading:true
            },
            articleData:{},
            editor:null,
        }
    },
    activated(){
        console.log("detailView activated");
    },
    created(){
        this.$store.commit("SET_LOADING_INIT",(false))
        let id = this.$route.params.id
        let _this = this
        getArticleById(id).then(res=>{
            _this.articleData = res.data.data[0]
            _this.$nextTick(()=>{
                _this.initEditorView()
            })
            setTimeout(()=>{
                _this.show.loading = false
            },500)
        }).catch(err =>{
            console.log(err);
        })
    },
    methods:{
        initEditorView(){
            this.editor  = new Viewer({
                el: document.querySelector('#editorView'),
                height: 'auto',
                initialValue: this.articleData.a_md,
                plugins: [[codeSyntaxHighlight, { highlighter: Prism }]]
            });
        }
    },  
    mounted(){
        
    }

}
</script>

<style lang="scss" scoped>
.loading {
    width: 100%;
    height: 100%;
    position: relative;
    border-radius: 5px;

    // box-shadow: 0 0 3px 1px rgba(0,0,0,.05);
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        width: 25px;
        height: 25px;
        border: 1px solid transparent;
        border-top:2px solid rgba(0,0,0,.4);
        border-left:2px solid rgba(0,0,0,.4);
        border-radius: 50%;
        transform-origin:center ;
        animation: radius 1.5s linear infinite;
    }
}

@keyframes radius{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
.detailView {
    width: 100%;
    position: relative;
    overflow: hidden;
    background: #eee;
}

.detailContent {
    width: 100%;
    min-height: calc(100vh - 60px);
    max-width: 900px;
    margin: 0px auto ;
    position: relative;
    z-index: 1;
    border-radius: 5px;
}


.detailContent {
    padding: 20px;
    .detailMain{
        width: 100%;
        min-height: calc(100vh - 140px);
        margin: 0 auto;
        background: rgba(255,255,255,.8);
        padding: 20px;
        border-radius: 5px;
        .topMenu {
            font-size: 14px;
            color: #666;
            margin-bottom: 10px;
            span {
                cursor: pointer;
            }
        }
        .title {
            font-size: 20px;
            margin-bottom: 20px;
        }
    }
    .loadingView {
        width: 100%;
        height: calc(100vh - 140px);
        padding: 20px;
        background: #fff;
    }
}


 #particles-js, .particles-js-canvas-el {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: 0;
    }
</style>