<template>
    <div class="myArticleView">
        <div class="header">
            <div class="info">
                <img src="../assets/public/img/avater.jpg" alt="">
                <div class="detail">
                    <div class="name">Cc  </div>
                    <div class="desc">不以物喜，不以己悲</div>
                </div>
            </div>
        </div>

        <div class="contentView">
            
            <vue-particles
                color="#409EFF"
                :particleOpacity="0.7"
                :particlesNumber="60"
                shapeType="circle"
                :particleSize="6"
                linesColor="#409EFF"
                :linesWidth="1"
                :lineLinked="true"
                :lineOpacity="0.4"
                :linesDistance="150"
                :moveSpeed="3"
                :hoverEffect="true"
                hoverMode="grab"
                :clickEffect="true"
                clickMode="push">
            </vue-particles>

            <div class="articleList" v-show="!$store.state.lodingInit">
                <div class="articleItem" v-for="(item,index) in article.data" :style="{ 'animation-delay': (index*.2) +'s' }" :key="index" @click="toDetail(item.a_id)">
                    <div class="name">{{ item.a_title }}</div>
                    <div class="desc line2">{{ item.desc }}</div>
                    <div class="time">{{ item.a_create_time }}</div>
                </div>
            </div>

        </div>

        <!-- <edit /> -->
        
    </div>
</template>

<script>

import edit from "./admin/Edit.vue"
import { getArticle } from "../axios/api"

import { formatDate } from "../uitl/uitl"
export default {
    name:"MyArticle",
    components:{
        edit
    },
    data(){
        return {
            article:{}
        }
    },
    activated(){
        console.log("myArticleView activated");
    },
    created(){
        console.log("myArticleView created");
        getArticle().then(res =>{
            this.article = res.data.data
            this.article.data.forEach(item =>{
                item.a_create_time = formatDate('yyyy-MM-dd hh:mm:ss',item.a_create_time)
                item.desc = item.a_md.slice(0,400)
                item.desc = item.desc.replaceAll("#",'').replaceAll("##",'')
                .replaceAll("###",'')
                .replaceAll("```js",'')
                .replaceAll("```",'')
                
            })

            setTimeout(()=>{
                this.$store.commit("SET_LOADING_INIT",(false))
            },2000)
        }).catch(err =>{
            console.log(err);
        })
    },
    methods:{
        toDetail(aid){
            this.$router.push({
                path:`/home/detail/${aid}`,
            })
        }
    }
}

</script>

<style lang="scss" scoped>

    
    .header {
        width: 100%;
        height: 350px;
        background: url(../assets/public/img/bgA.jpg) no-repeat center;
        background-size: cover;
        padding-top: 80px;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            backdrop-filter: blur(3px);
            z-index: 0;
        }
        .info{
            margin-bottom: 20px;
            position: relative;
            z-index: 1;
            >img {
                display: block;
                width: 100px;
                height: 100px;
                border-radius: 50%;
                border: 2px solid #eee;
                box-shadow: 0 0 3px 2px #eee;
                margin: 0 auto 20px;
            }
            .name {
                text-align: center;
                font-size: 26px;
                color: #eee;
                margin-bottom: 10px;
            }
            .desc {
                font-size: 18px;
                color: #EEE;
                margin: 0 auto;
                text-align: center;
            }
        }
        
    }

   

    .contentView {
        width: 100%;
        min-height: calc(100vh - 410px );
        position: relative;
        background: #eee;
        // background: ;
       
    }
    #particles-js, .particles-js-canvas-el {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
    }

    .articleList {
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
        padding: 20px;
        position: relative;
        z-index: 1;
        .articleItem {
            padding: 10px ;
            box-shadow: 0 0 5px 2px rgba(0,0,0,.1);
            line-height: 1.2em;
            border-radius: 8px;
            background: rgba(255,255,255,.8);
            cursor: pointer;
            margin-bottom: 20px;
            animation: fade .5s ;
            opacity: 0;
            animation-fill-mode: forwards;
            .name {
                font-size: 18px;
                color: #111;
                margin-bottom: 10px;
            }
            .desc {
                font-size: 15px;
                color: #666;
                margin-bottom: 5px;
            }
            .time {
                font-size: 13px;
                color: #888;
            }
        }
    }

    @keyframes fade {
        0%{
            opacity: 0;
            transform: translateY(50px);
        }
        100%{   
            opacity: 1;
            transform: translateY(0px);
        }
    }

    @media screen and (max-width: 600px) {
        .header {
            height: 200px;
            padding-top: 40px;
            .info{
                display: flex;
                margin-bottom: 10px;
                align-items: center;
                img {
                    box-shadow: 0 0 3px 2px #eee;
                    margin: 0 20px 0 ;
                }
                .detail {

                }
                .name {
                    text-align: left;
                    font-size: 16px;
                    color: #eee;
                    margin-bottom: 10px;
                }
                .desc {
                    font-size: 16px;
                }
            }
        }
        .contentView { 
            min-height: calc(100vh - 260px );
        }

    }



</style>